<template>
  <div class="notice__wrapper" @click="$event.stopPropagation()">
    <div class="notice__util">
      <div class="notice__util__tabs">
        <button type="button" :class="{ 'active': 0 === activeIndex }" @click="toggleTab(0, 'D02001')"></button>
        <button type="button" :class="{ 'active': 1 === activeIndex }" @click="toggleTab(1, 'D02002')"></button>
        <button type="button" :class="{ 'active': 2 === activeIndex }" @click="toggleTab(2, 'D02003')"></button>
        <button type="button" :class="{ 'active': 3 === activeIndex }" @click="toggleTab(3, 'D02004')"></button>
        <button type="button" :class="{ 'active': 4 === activeIndex }" @click="toggleTab(4, 'D02005')"></button>
      </div>
    
      <div class="notice__util__cont">
        <div class="cont" v-if="0 === activeIndex">
          <div class="notice__util__cont-info">
            <ui-checkbox :name="'checkAll'"
                         :id="'postLikeCheckAll'"
                         :label="' '"
                         :clazz="['notice-check-all']"
                         :values="'Y,N'"
                         v-model="checkAll"
                         v-if="0 < postLikeList.length">
            </ui-checkbox>
  
            <ui-icon-button :type="'button'"
                            :color="'light'"
                            :icon="'xi-check'"
                            :class="'btn-notice-check'"
                            @click="updateCheckYn"
                            v-if="0 < postLikeList.length">
            </ui-icon-button>
  
            <p class="notice-count__wrapper"> <span class="notice-count">{{ postLikeList.length }}</span>   </p>
          </div>
  
          <ul v-if="0 < postLikeList.length">
            <li v-for="(noti,i) in postLikeList" :key="i">
              <ui-checkbox :name="'checkYn'"
                           :id="`postLikeNoticeCheck${noti.id}`"
                           :label="' '"
                           :labelHidden="true"
                           :value="noti.id"
                           v-model="idList">
              </ui-checkbox>
  
              <router-link :to="noti.link">{{ noti.title }}</router-link>   .
              <span class="info">{{ noti.regDate }}</span>
            </li>
          </ul>
  
          <!-- <ui-icon-button :type="'button'"
                          :icon="'xi-ellipsis-h'"
                          :text="' '"
                          :class="'btn-more'">
          </ui-icon-button> -->
        </div>
    
        <div class="cont" v-if="1 === activeIndex">
          <div class="notice__util__cont-info">
            <ui-checkbox :name="'checkAll'"
                         :id="'postReplyCheckAll'"
                         :label="' '"
                         :clazz="['notice-check-all']"
                         :values="'Y,N'"
                         v-model="checkAll"
                         v-if="0 < postReplyList.length">
            </ui-checkbox>
  
            <ui-icon-button :type="'button'"
                            :color="'light'"
                            :icon="'xi-check'"
                            :class="'btn-notice-check'"
                            @click="updateCheckYn"
                            v-if="0 < postReplyList.length">
            </ui-icon-button>
  
            <p class="notice-count__wrapper"> <span class="notice-count">{{ postReplyList.length }}</span>   </p>
          </div>

          <ul v-if="0 < postReplyList.length">
            <li v-for="(noti,i) in postReplyList" :key="i">
              <ui-checkbox :name="'checkYn'"
                           :id="`postReplyNoticeCheck${noti.id}`"
                           :label="' '"
                           :labelHidden="true"
                           :value="noti.id"
                           v-model="idList">
              </ui-checkbox>
              
              <router-link :to="noti.link">{{ noti.title }}</router-link>   .
              <span class="info">{{ noti.regDate }}</span>
            </li>
          </ul>
          
          <!-- <ui-icon-button :type="'button'"
                          :icon="'xi-ellipsis-h'"
                          :text="' '"
                          :class="'btn-more'">
          </ui-icon-button> -->
        </div>
    
        <div class="cont" v-if="2 === activeIndex">
          <div class="notice__util__cont-info">
            <ui-checkbox :name="'checkAll'"
                         :id="'guestbookCheckAll'"
                         :label="' '"
                         :clazz="['notice-check-all']"
                         :values="'Y,N'"
                         v-model="checkAll"
                         v-if="0 < guestbookList.length">
            </ui-checkbox>
  
            <ui-icon-button :type="'button'"
                            :color="'light'"
                            :icon="'xi-check'"
                            :class="'btn-notice-check'"
                            @click="updateCheckYn"
                            v-if="0 < guestbookList.length">
            </ui-icon-button>
  
            <p class="notice-count__wrapper"> <span class="notice-count">{{ guestbookList.length }}</span>   </p>
          </div>

          <ul v-if="0 < guestbookList.length">
            <li v-for="(noti,i) in guestbookList" :key="i">
              <ui-checkbox :name="'checkYn'"
                           :id="`guestbookNoticeCheck${noti.id}`"
                           :label="' '"
                           :labelHidden="true"
                           :value="noti.id"
                           v-model="idList">
              </ui-checkbox>{{ noti.senderNm }} <router-link :to="noti.link"> .</router-link>

              <span class="info">{{ noti.regDate }}</span></li>
          </ul>
          
          <!-- <ui-icon-button :type="'button'"
                          :icon="'xi-ellipsis-h'"
                          :text="' '"
                          :class="'btn-more'">
          </ui-icon-button> -->
        </div>
    
        <div class="cont" v-if="3 === activeIndex">
          <div class="notice__util__cont-info">
            <ui-checkbox :name="'checkAll'"
                         :id="'guestbookReplyCheckAll'"
                         :label="' '"
                         :clazz="['notice-check-all']"
                         :values="'Y,N'"
                         v-model="checkAll"
                         v-if="0 < guestbookReplyList.length">
            </ui-checkbox>
  
            <ui-icon-button :type="'button'"
                            :color="'light'"
                            :icon="'xi-check'"
                            :class="'btn-notice-check'"
                            @click="updateCheckYn"
                            v-if="0 < guestbookReplyList.length">
            </ui-icon-button>
  
            <p class="notice-count__wrapper"> <span class="notice-count">{{ guestbookReplyList.length }}</span>   </p>
          </div>

          <ul v-if="0 < guestbookReplyList.length">
            <li v-for="(noti,i) in guestbookReplyList" :key="i">
              <ui-checkbox :name="'checkYn'"
                           :id="`guestbookReplyNoticeCheck${noti.id}`"
                           :label="' '"
                           :labelHidden="true"
                           :value="noti.id"
                           v-model="idList">
              </ui-checkbox>{{ noti.senderNm }} <router-link :to="noti.link">  .</router-link>

              <span class="info">{{ noti.regDate }}</span></li>
          </ul>
          
          <!-- <ui-icon-button :type="'button'"
                          :icon="'xi-ellipsis-h'"
                          :text="' '"
                          :class="'btn-more'">
          </ui-icon-button> -->
        </div>

        <div class="cont" v-if="4 === activeIndex">
          <div class="notice__util__cont-info">
            <ui-checkbox :name="'checkAll'"
                         :id="'satisfactionCheckAll'"
                         :label="' '"
                         :clazz="['notice-check-all']"
                         v-model="checkAll"
                         v-if="0 < satisfactionList.length">
            </ui-checkbox>
  
            <ui-icon-button :type="'button'"
                            :color="'light'"
                            :icon="'xi-check'"
                            :class="'btn-notice-check'"
                            @click="updateCheckYn"
                            v-if="0 < satisfactionList.length">
            </ui-icon-button>
  
            <p class="notice-count__wrapper"> <span class="notice-count">{{ satisfactionList.length }}</span>   </p>
          </div>

          <ul v-if="0 < satisfactionList.length">
            <li v-for="(noti,i) in satisfactionList" :key="i">
              <ui-checkbox :name="'checkYn'"
                           :id="`satisfactionNoticeCheck${noti.id}`"
                           :label="' '"
                           :labelHidden="true"
                           :value="noti.id"
                           v-model="idList">
              </ui-checkbox>
              
              <router-link :to="noti.link">{{ noti.title }}</router-link>  <router-link :to="'/admin/satisfaction'">  .</router-link>
              <span class="info">{{ noti.regDate }}</span></li>
          </ul>
          
          <!-- <ui-icon-button :type="'button'"
                          :icon="'xi-ellipsis-h'"
                          :text="' '"
                          :class="'btn-more'">
          </ui-icon-button> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { isNotEmpty, messageUtil } from '@/utils';

  export default {
    name: 'app-notification',
    props: {
      /**   */
      list: Array,
    },
    data() {
      return {
        /**     */
        postLikeList: [],
        /**     */
        postReplyList: [],
        /**    */
        guestbookList: [],
        /**     */
        guestbookReplyList: [],
        /**    */
        satisfactionList: [],
        /**   Index */
        activeIndex: 0,
        /**      */
        activeTypeCd: '',
        /**    ID  */
        idList: [],
      }
    },
    created() {
      this.setData();
    },
    computed: {
      /**    */
      checkAll: {
        get() {
          if (isNotEmpty(this.idList) && 0 < this.idList.length) {
            return this.idList.length === this.list.filter(d => d.typeCd === this.activeTypeCd).length;
          }
          return false;
        },
        set(v) {
          this.idList = [];

          if ('Y' === v) {
            this.list.filter(d => d.typeCd === this.activeTypeCd).map(d => {
              this.idList.push(d.id);
            });
          }
        }
      },
    },
    methods: {
      /**    */
      setData() {
        this.activeTypeCd = 'D02001';

        this.list.map(d => {
          d.regDate = this.$moment(d.regDate).format('YYYY-MM-DD HH:mm:ss');
        });

        this.postLikeList = this.list.filter(d => d.typeCd === 'D02001');         //    
        this.postReplyList = this.list.filter(d => d.typeCd === 'D02002');        //    
        this.guestbookList = this.list.filter(d => d.typeCd === 'D02003');        //   
        this.guestbookReplyList = this.list.filter(d => d.typeCd === 'D02004');   //    
        this.satisfactionList = this.list.filter(d => d.typeCd === 'D02005');     //   
      },
      /**     */
      updateCheckYn() {
        if (0 === this.idList.length) {
          messageUtil.toastWarning(' .');
          return;
        }

        return this.$http.put('/notification', this.idList)
        .then(res => {
          this.$emit('check');
        });
      },
      /**  toggle */
      toggleTab(idx, typeCd) {
        this.activeIndex = idx;
        this.activeTypeCd = typeCd;
        this.idList = [];
      },
    },
  }
</script>

<style lang="scss" scoped>
@import '@/assets/scss/components/notice.scss';
</style>