import Swal from 'sweetalert2';

/**
 *  
 */
class MessageUtil {

  _alert = null;
  _confirm = null;
  _toast = null;

  constructor() {
    this._alert = Swal.mixin({});

    this._confirm = Swal.mixin({
			showCancelButton: true,
			confirmButtonText: '',
			cancelButtonText: '',
		});

    this._toast = Swal.mixin({
      toast: true,
      width: 450,
      position: 'top-end',
      showConfirmButton: false,
      timer: 2000,
    });
  }

  /** Alert   */
  alertSuccess(title, msg = '') {
    return new Promise((resolve, reject) => {
			this._alert.fire({
				title: title,
				text: msg,
				icon: 'success',
			}).then(result => resolve(result.isConfirmed));
		});
  }

  /** Alert   */
  alertError(title, msg = '') {
    return new Promise((resolve, reject) => {
			this._alert.fire({
				title: title,
				text: msg,
				icon: 'error',
			}).then(result => resolve(result.isConfirmed));
		});
  }

  /** Alert   */
  alertWarning(title, msg = '') {
    return new Promise((resolve, reject) => {
			this._alert.fire({
				title: title,
				text: msg,
				icon: 'warning',
			}).then(result => resolve(result.isConfirmed));
		});
  }

  /** Alert   */
  alertInfo(title, msg = '') {
    return new Promise((resolve, reject) => {
			this._alert.fire({
				title: title,
				text: msg,
				icon: 'info',
			}).then(result => resolve(result.isConfirmed));
		});
  }

  /** Alert   */
  alertQuestion(title, msg = '') {
    return new Promise((resolve, reject) => {
			this._alert.fire({
				title: title,
				text: msg,
				icon: 'question',
			}).then(result => resolve(result.isConfirmed));
		});
  }

  /** Confirm   */
  confirmSuccess(title, msg = '') {
    return new Promise((resolve, reject) => {
			this._confirm.fire({
				title: title,
				text: msg,
				icon: 'success',
			}).then(result => resolve(result.isConfirmed));
		});
  }

  /** Confirm   */
  confirmError(title, msg = '') {
    return new Promise((resolve, reject) => {
			this._confirm.fire({
				title: title,
				text: msg,
				icon: 'error',
			}).then(result => resolve(result.isConfirmed));
		});
  }

  /** Confirm   */
  confirmWarning(title, msg = '') {
    return new Promise((resolve, reject) => {
			this._confirm.fire({
				title: title,
				text: msg,
				icon: 'warning',
			}).then(result => resolve(result.isConfirmed));
		});
  }

  /** Confirm   */
  confirmInfo(title, msg = '') {
    return new Promise((resolve, reject) => {
			this._confirm.fire({
				title: title,
				text: msg,
				icon: 'info',
			}).then(result => resolve(result.isConfirmed));
		});
  }

  /** Confirm   */
  confirmQuestion(title, msg = '') {
    return new Promise((resolve, reject) => {
			this._confirm.fire({
				title: title,
				text: msg,
				icon: 'question',
			}).then(result => resolve(result.isConfirmed));
		});
  }

  /** Toast   */
  toastSuccess(msg) {
    this._toast.fire({
      icon: 'success',
      title: msg,
    });
  }

  /** Toast   */
  toastError(msg) {
    this._toast.fire({
      icon: 'error',
      title: msg,
    });
  }

  /** Toast   */
  toastWarning(msg) {
    this._toast.fire({
      icon: 'warning',
      title: msg,
    });
  }

  /** Toast   */
  toastInfo(msg) {
    this._toast.fire({
      icon: 'info',
      title: msg,
    });
  }

  /** Toast   */
  toastQuestion(msg) {
    this._toast.fire({
      icon: 'question',
      title: msg,
    });
  }
	
}

export const messageUtil = new MessageUtil();
