<template>
  <loading v-model:active="active"
           :can-cancel="cancel"
           :is-full-page="fullPage"
           :loader="loader"
           :color="color">
  </loading>
</template>

<script>
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/css/index.css';

export default {
  name: 'ui-loading',
  components: {
    Loading,
  },
  computed: {
    active: {
      get() {
        return this.activeModel;
      },
      set(v) {}
    }
  },
  props: {
    /**    */
    activeModel: {
      type: Boolean,
      default: false,
    },
    /**      */
    cancel: {
      type: Boolean,
      default: true,
    },
    /**      */
    fullPage: {
      type: Boolean,
      default: false,
    },
    /**   (spinner, dots, bars) */
    loader: {
      type: String,
      default: 'spinner',
    },
    /**   */
    color: {
      type: String,
      default: '#000',
    }
  }
}
</script>

<style lang="scss" scoped>

</style>