<template>
  <SlickList :axis="axis"
             :pressDelay="pressDelay"
             :shouldCancelStart="shouldCancelStart"
             v-model:list="mv"
             @sort-start="onSortStart"
             @update:list="onUpdateList">
    <slot></slot>
  </SlickList>
</template>

<script>
import { SlickList } from 'vue-slicksort';

export default {
  name: 'ui-sort-list',
  components: {
    SlickList,
  },
  props: {
    /**   */
    axis: {
      type: String,
      default: 'y',
    },
    /** delay */
    pressDelay: Number,
    /**   input, button     (callback   HTML   ) */
    shouldCancelStart: Function,
    /** v-model  */
    modelValue: Array,
  },
  computed: {
    mv: {
      get() {
        return this.modelValue;
      },
      set(v) {}
    }
  },
  methods: {
    /**    */
    onSortStart() {
      this.$emit('sortStart');
    },
    /**    */
    onUpdateList(list) {
      this.$emit('updateList', list);
    },
  },
}
</script>

<style lang="scss" scoped>

</style>