export default [
  {
    //   
    path: '/year',
    component: () => import('@/views/AppYear.vue'),
  },
  {
    //   
    path: '/category/:id',
    component: () => import('@/views/AppCategory.vue'),
    props: (route) => ({
      pageType: 'D01004',
      id: route.params.id,
    }),
  },
  {
    //   
    path: '/tag/:id',
    component: () => import('@/views/AppCategory.vue'),
    props: (route) => ({
      pageType: 'D01005',
      id: route.params.id,
    }),
  },
];
