<template>
  <div class="file-upload__wrapper">
    <ui-file-field :name="'uploadFiles'"
                   :id="'uploadFiles'"
                   :title="'  '"
                   :accept="'image/*'" multiple
                   :hidden="true"
                   @onchange="onChange">
    </ui-file-field>

    <div :class="{
      'file-upload__list': true,
      'upload-list': true,
      'file-upload__list--active': isDragged
    }"
      @dragenter="onDragEnter"
      @dragover="onDragOver"
      @dragleave="onDragLeave"
      @drop="onDrop"
    >
      <div class="file-upload__list__inner">
        <label for="uploadFiles" class="file-upload--attach">
          <strong>  &amp;     .</strong>
        </label>

        <ul>
          <li v-for="(file,i) in fileList" :key="i">
            <span class="file-upload__list__file-name"
                  @mouseenter="toggleImage(i)"
                  @mouseleave="hideImage(i)">{{ file.name }}

              <span class="file-upload__list__file-info" v-show="i === activeIndex">
                <img :src="file.src" :alt="file.name">
              </span>
            </span>
            
            <ui-icon-button :type="'button'"
                            :icon="'xi-close'"
                            :text="' '"
                            :class="'file-upload--remove'"
                            @click="onRemove(i)">
            </ui-icon-button>
          </li>
        </ul>
      </div>

      <div class="file-upload__btns">
        <ui-button :type="'button'"
                   :color="'secondary'"
                   :class="'file-upload__btn file-upload__btn--reset'"
                   @click.stop="onReset">
        </ui-button>
  
        <ui-button :type="'button'"
                   :color="'primary'"
                   :class="'file-upload__btn file-upload__btn--submit'"
                   @click.stop="onSubmit">
        </ui-button>
      </div>
    </div>
  </div>
</template>

<script>
import { messageUtil } from '@/utils';

export default {
  name: 'ui-file-upload-field',
  data() {
    return {
      /**   */
      fileList: [],
      /**     */
      isDragged: false,
      /**    index */
      activeIndex: -1,
    }
  },
  methods: {
    onDragEnter() {
      this.isDragged = true;
    },
    onDragLeave() {
      this.isDragged = false;
    },
    onDragOver(e) {
      e.preventDefault();
    },
    onDrop(e) {
      e.preventDefault();

      this.isDragged = false;
      const files = e.dataTransfer.files;
      this.addFiles(files);
    },
    /**  input    */
    onChange(files) {
      this.addFiles([files]);
    },
    /**   */
    async addFiles(files) {
      for (const file of files) {
        const isValid = this.addFileValidationCheck(file);
        if (!isValid) return;

        const src = await this.readFiles(file);
        file.src = src;
        
        this.fileList.push(file);
      }
    },
    /**   */
    async readFiles(files) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = (e) => {
          resolve(e.target.result);
        }
        reader.readAsDataURL(files);
      });
    },
    /**   */
    onRemove(idx) {
      this.fileList.splice(idx, 1);
    },
    /**    */
    onReset() {
      this.fileList = [];
    },
    /**   */
    toggleImage(idx) {
      if (idx === this.activeIndex) {
        this.activeIndex = -1;
        return;
      }
      
      this.activeIndex = idx;
    },
    /**    */
    hideImage(idx) {
      if (idx === this.activeIndex) {
        this.activeIndex = -1;
      }
    },
    /**    */
    async onSubmit() {
      const isValid = this.submitValidationCheck();
      if (!isValid) return;

      const confirm = await messageUtil.confirmSuccess('?');
      if (!confirm) return;

      for (const file of this.fileList) {
        //  src      .
        delete file.src;
      }

      const headers = { 'Content-Type': 'multipart/form-data' };

      this.$http.post('/file', this.fileList, { headers })
      .then(res => {
        messageUtil.toastSuccess('.');

        this.onReset();
        this.$emit('upload');
      });
    },
    /**     */
    addFileValidationCheck(file) {
      if (!file.name.match(/(.*?)\.(jpg|jpeg|png)$/)) {
        messageUtil.toastWarning('jpg, jpeg, png   .');
        return false;
      }

      return true;
    },
    /**      */
    submitValidationCheck() {
      if (0 === this.fileList.length) {
        messageUtil.toastWarning(' .');
        return false;
      }

      return true;
    },
  },
}
</script>

<style lang="scss" scoped>
@import '@/assets/scss/views/upload.scss';
</style>