<template>
  <Form :name="name"
        :ref="name"
        :id="name"
        :autocomplete="autocomplete"
        @submit="onSubmit"
        @reset="onReset"
        @keydown.enter="onEnter"
  >
    <slot :validateAll="validateAll"
          :validateField="validateField">
    </slot>
  </Form>
</template>

<script>
import { getFormValues, isEmpty } from '@/utils';
import { Form } from 'vee-validate';

export default {
  name: 'ui-form',
  components: {
    Form,
  },
  props: {
    /** form name */
    name: String,
    /** form autocomplete */
    autocomplete: String,
    /** form Enter submit   */
    preventEnter: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      /** form field   */
      formOldValues: null,
    }
  },
  mounted() {
    const form = this.$refs[this.name].$el;
    this.formOldValues = getFormValues(form);
  },
  methods: {
    /** form   */
    onSubmit(values) {
      this.$emit('onsubmit', values);
    },
    /** form reset  */
    onReset() {
      /**
       * 2023.01.14.
       * vee-validate form reset ,      reset    
       *   -    form    .
       */
      for (const key of Object.keys(this.formOldValues)) {
        this.$refs[this.name].setFieldValue(key, this.formOldValues[key]);
      }

      this.$emit('onreset');
    },
    /** Enter submit  */
    onEnter(e) {
      // Markdown    Enter    
      if (this.editorClassName === e.target.id) {
        return true;
      }

      if (this.preventEnter) {
        e.preventDefault();
        return false;
      }

      return true;
    },
    /**  Field    */
    validateAll() {
      return this.$refs[this.name].validate();
    },
    /** 1 Field    */
    validateField(field) {
      return this.$refs[this.name].validateField(field);
    },
    /** Field   */
    setFieldValue(field, value) {
      if (isEmpty(field)) return null;

      return this.$refs[this.name].setFieldValue(field, value);
    },
    /** Field   */
    getFieldValue(field) {
      if (isEmpty(field)) return null;

      const form = this.$refs[this.name].$el;
      const formValues = getFormValues(form);
      
      return formValues[field];
    }
  },
}
</script>

<style lang="scss" scoped>

</style>